import React from 'react'

import TwitterIcon from '@mui/icons-material/Twitter';
import Like from '../Like/Like.jsx'
import './tweet.scss'
const Tweet = () => {
    const tweets = [{
        "content": "I will be leaving the great Walter Reed Medical Center today at 6:30 P.M. Feeling really good! Dont be afraid of Covid. Dont let it dominate your life. We have developed, under the Trump Administration, some really great drugs & knowledge. I feel better than I did 20 years ago!",
        "date": "October 5, 2020",
    },
    {
        "content": "Starting to get VERY high marks in our handling of the Coronavirus (China Virus), especially when compared to other countries and areas of the world. Now the Vaccines (Plus) are coming, and fast!",
        "date": "September 7, 2020"
    },
    {
        "content": "Crazy Joe Biden is trying to act like a tough guy. Actually, he is weak, both mentally and physically, and yet he threatens me, for the second time, with physical assault. He doesnt know me, but he would go down fast and hard, crying all the way. Dont threaten people Joe!",
        "date": "March 22, 2018"
    },
    {
        "content": "These are the things and events that happen when a sacred landslide election victory is so unceremoniously & viciously stripped away from great patriots who have been badly & unfairly treated for so long. Go home with love & in peace. Remember this day forever!",
        "date": "January 6, 2021"
    }
    ]


  return (
    <>  
        <div className="header">
            <TwitterIcon className='tweet-header-icon'/> <h1 className="tweets-header">Best Tweets</h1>
        </div>
        {tweets.map((tweet, i) =>
            <div className='tweet-wrapper'>
                <div className="tweet-main" key={i}>
                    <div className="profile-section">
                        <div className="image">
                            <img src="data:image/jpeg;base64," alt="" className="profile-image" />
                        </div>
                        <div className="profile-details">
                            <h2 className="poster">Donald Trump</h2>
                            <span className="username">@RealGDonny</span>
                        </div>
                    </div>
                        
                    <div className="tweet-content">
                        {tweet.content}
                    </div>
                    <div className="reactions">
                        <div className="date">
                            {tweet.date}
                        </div>
                        <div className="right">
                            <Like />
                            
                        </div>
                        
                    </div>
                </div>
            </div>
        )}
    </>
    
  )
}

export default Tweet